#Resume{
    padding-top: 50px;
    padding-bottom: 500px;
    margin-top: 300px;
}
#sp{
    box-sizing: border-box;
    font-size: 24px;
    border-radius: 5px;
    box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 10px;
    background-color: #31A8FF;
}

#link{
    color: black;
    text-decoration: none;
}
#sp:hover{
    background-color: #2b8fd7;
}
#link:hover{
    color: blue;
}